import React, { Component } from 'react';
import { Transition } from 'react-transition-group';
import './App.css';
import Modal from './components/Modal/Modal';
import Backdrop from './components/Backdrop/Backdrop';
import List from './components/List/List';

const duration = 2000;

const defaultStyle = {
  transition: `opacity ${duration}ms ease-out`,
  opacity: 0,
  backgroundColor: 'burlywood',
  width: 100,
  height: 100,
  margin: 'auto',
};

const transitionStyles = {
  entering: { opacity: 1 },
  entered: { opacity: 1 },
  exiting: { opacity: 0 },
  exited: { opacity: 0 },
};

class App extends Component {
  state = { modalIsOpen: false, showBlock: false };

  showModal = () => this.setState({ modalIsOpen: true });

  closeModal = () => this.setState({ modalIsOpen: false });

  render() {
    const { modalIsOpen, showBlock } = this.state;

    return (
      <div className="App">
        <h1>React Animations</h1>
        <button
          className="Button"
          onClick={() =>
            this.setState((prevState) => ({ showBlock: !prevState.showBlock }))
          }
        >
          Toggle
        </button>
        <br />
        <Transition
          in={showBlock}
          timeout={duration}
          mountOnEnter
          unmountOnExit
          onEnter={() => console.log('onEnter')}
          onEntering={() => console.log('onEntering')}
          onEntered={() => console.log('onEntered')}
          onExit={() => console.log('onExit')}
          onExiting={() => console.log('onExiting')}
          onExited={() => console.log('onExited')}
        >
          {(state) => (
            <div style={{ ...defaultStyle, ...transitionStyles[state] }}></div>
          )}
        </Transition>

        <Modal show={modalIsOpen} closed={this.closeModal} />
        {modalIsOpen && <Backdrop show />}
        <button className="Button" onClick={this.showModal}>
          Open Modal
        </button>
        <h3>Animating Lists</h3>
        <List />
      </div>
    );
  }
}

export default App;



