import React from 'react';

import './Backdrop.css';

const backdrop = ({ show }) => {
  const cssClasses = ['Backdrop', show ? 'BackdropOpen' : 'BackdropClosed'];

  return <div className={cssClasses.join(' ')}></div>;
};

export default backdrop;



